@import '~antd/dist/antd.css';
*{
    padding: 0;
    margin: 0;
}
.car{
    width: 100%;;
    position: relative;
}

  .bar {
    position: absolute;
    top:0px;

    height: 44px; padding: 0 10px;
    background-color: #433333; opacity: 0.8; /* 搜索框半透明效果 */
    z-index: 10;
  }
  .barinput{
   width: 70%;
   height: 30px;
  
  }
  .search {
   
    width: 80%;
    height: 30px;
    border-radius: 8px;
    outline: none;
    border: 1px solid #ccc;
    background-color: #ccc;
    margin-top: 8px;
    margin-left: 10px;
    
}
#ss{
    width: 110px;
    height: 30px;
    position: absolute;
    top: 10px;
    left: 50px;
    color: #000;
}


 

